.usage-skill {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
     
    &_size {
        width: 350px;
        height: 70px;
    }
    &__wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;  
    } 
    &__percent {
        font-size: 14px;
        font-weight: 500;
    }
}