.resume {
    padding-bottom:  60px;
    &__wrapper {
        padding:  60px 65px 0  65px;
        display: grid;
        grid-template-columns: repeat(2,425px);
        column-gap: 135px;
    }
    &__column {

    }
    &__list {
        list-style-type: none;
    }
    &__list-item {
        position: relative;
        &:after {
           content: " ";
           position: absolute;
           height: 100%;
           width: 1px;
           background-color: black;
           left: -43px;   
           top: 0;  
        }
    }
    &__list-wrapper {

    }
    &__list-head {
        // padding-top: 59px;
        // padding-left: 100px;
        // padding-bottom: 17px;
        padding: 51px 0 17px 57px;
    }
    &__list-location {
        font-size: 12px;
        font-weight: 500;
        line-height: 18px;
        font-style: normal;
    }
    &__list-body {
        line-height: 18px;
        font-size: 12px;
        font-weight: 500;
        font-style: normal;
    }
}