.circle {
    border-radius: 100%;

    &__resume__list-head {
        position: absolute;
        left:0;
        top: 53px;
    }    
    &_bg_or {
        background-color: $main-color;
    }
    &_center {
        margin: 0 auto;
    }
    &_text {
        color: #000;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }
    &_size {
        width: 36px;
        height: 36px;
    }
    &_size_sml {
        width: 15px;
        height: 15px;
        border-radius:100%;
    }
    // elements
    &__wrapper {
        height: 100%;
        position: relative;
        &_shift {
          left: -50px;  
        }
        &_before {
            &:before {
                content: " ";
                position: absolute;
                left: -21px;
                top: 50%;
                transform: translate(-50%);
                height: 1px;
                width: calc(100% + 7px) ;
                background-color:#000;
            }
        }
    }
   
    &__text {
        position: absolute;
        color: #000;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        margin-bottom: 0;
        &_fz_16 {
            font-size: 16px;
        }
        &_fz_14 {
            font-size: 14px;
        }
        &_fz_20 {
            font-size: 20px;
            line-height: 30px;
        }
        &_right {
            left: 12px;
            top: 50%;
            transform: translateY(-50%);
            width: 245px;
        }
        &_right_ln {
            padding-left: 35px;
            left: 15px;
            top: 50%;
            transform: translateY(-50%);
        }
        &_right_mdl {
            padding-left: 15px;
            left: 15px;
            top: 50%;
            transform: translateY(-50%);
        }
        &_center {
            top: 50%;
            left: -2px;
            transform: translateY( -50%);
        }
        &_center_4 {
            left: -82%;
        }
        &_center_3 {
            left: -75%;
        }
        &_center_2 {
            left: -30%;
        }
        &_size {
            width: 200px;
        }
        &_size_1 {
            width: 150px;
        }
        &_size_2 {
            width: 250px;
        }
    }
    &__img {
        position: absolute;
        &_right {
            left: 50%;
            top: 41%;
            
        }
 
        &_hidden {
            display: none;
        } 
    }
}