.arrow-up {
    z-index: 9999;
    width: 70px;
    height: 70px;
    color: #FFF;
    position: fixed;
    top: calc(90% - 35px);
    right: -40px;
    visibility: hidden;
    opacity: 0;
    cursor: pointer;
    border:3px solid #BFE2FF;
    border-radius:100%;
    transform: scale(0);
    transition: all .2s ease-in-out;
    background-position: center center;
    background-repeat: no-repeat;
    background-color: $main-color;
    background-image: url(data:image/svg+xml;utf8;base64,);
    &:hover {
        transform: scale(1.2)!important;
    }
    &__active {
        visibility: visible;
        transform: scale(1);
        opacity: 1;
        right: 30px;
    }
}
