.contact {
    padding-bottom: 90px;
    &__wrapper {
        //padding: 0 35px 0 50px;
        display: grid;
        grid-template-columns: 400px 585px;
        justify-content: space-between;
    }
    &__img-wrapper {
        position: relative;
    }
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__link {
        display: inline-block;
        margin-right: 25px;
        height: 30px;
        width: 30px;
    }
    &__social-img {
        height: 100%;
        width: 100%;
    }
    &__text {
        font-size: 14px;
        font-weight: bold;
        line-height: 21px;
        margin-top: 20px;
        margin-bottom: 20px;
    }
    &__form {
        display: grid;
        grid-template-columns: 280px 280px;
        justify-content: space-between;
        row-gap: 26px;
    }
    &__input-wrapper {
        border: 1px solid black;
        position: relative;
        display: inline-block;
        z-index: 1000;
    }
    &__label-input {
        font-size: 12px;
        font-weight: 500;
        position: absolute;
        padding: 0 7px 0 7px;
        left: 16px;
        top:-9px;
        z-index: 2000;
        background-color: #fff;
    }
    &__input {
        border: none;
        padding: 13px 24px 13px 24px;
        width: 100%;
    }
    &__input_style {
        font-weight: 500;
        font-size: 12px;
        outline: none;
    }
    &__area-wrapper {
        grid-column: 1/3;
        justify-self: stretch;
        position: relative;
        z-index: 1000;
    }
    &__area {
      resize: none;
      padding: 7px 13px;
      width: 100%;
      height: 200px;
    }
    &__trigger {
        grid-column: 1/3;
        display: grid;
        grid-template-columns: auto auto;
        column-gap: 35px;
    }
    &__policy-container {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__policy {
        font-size: 12px;
        padding-left: 5px;
       
    }
}