.toggle-switch {
    display: inline-block;
    position: absolute;
    z-index: 1000;
    right: 100px;
    top: 17px;
    &_size_m {
        width: 30px;
        height: 17px;
    }
    &_size_s {
      width: 20px;
      height: 11px;
    }
    &_size_l {
       width: 59px;
       height: 27px;  
    }
    &__wraper {
        display: inline-block;
        width: 100%;
        height: 100%;
        position: relative;
    }
    &__input {
        height: 0;
        width: 0;
        opacity: 0;
    }
    &__slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transition: .4s; 
        background: url("../../icons/themes/dark.svg") no-repeat right center,
        url("../../icons/themes/light.svg")  no-repeat left center;
       
        &_theme_gray {
            background-color:white;  
            &:before {
                background-color: white;
                display: block;
                background-color:  rgba(70, 102, 190, 0.659);
                box-shadow: 2px 2px 6px blue;
            } 
         }
        &:before {
            position: absolute;
            content: "";
            transition: .4s;
        }
        &_size_l:before {
            height: 23px;
            width: 23px;
        }
        &_pos_l:before {
            left: 4px;
            bottom: 2px;
        }
        &_size_m:before {
            height: 13px;
            width: 13px;
        }  
        &_pos_m:before {
            left: 2px;
            bottom: 2px;
        }
        &_size_s:before {
          height: 9px;
          width: 9px; 
        }
        &_pos_s:before {
            left: 1px;
            bottom: 1px;
        }
        &_round_l {
            border-radius: 34px;
        }
        &_round_m {
            border-radius: 17px;
        }
        &_round_s {
            border-radius: 11px;
        }
        &_round_l:before, &_round_m:before, &_round_s:before{
            border-radius: 50%;
        }
    }
    // &__input:checked + &__slider_checked {
    //     background-color: #2196F3;
    // }
    &__input:focus +  &__slider_focused {
        box-shadow: 1px 1px 3px  #2196F3;
    }
    &__input:checked + &__slider_size_l:before {
        -webkit-transform: translateX(30px);
        -ms-transform: translateX(30px);
        transform: translateX(30px);
    }
    &__input:checked + &__slider_size_m:before {
        -webkit-transform: translateX(13px);
        -ms-transform: translateX(13px);
        transform: translateX(13px);
    }
    &__input:checked + &__slider_size_s:before {
        -webkit-transform: translateX(9px);
        -ms-transform: translateX(9px);
        transform: translateX(9px);
    }
}
  
