.scale {
    height: 14px;
    background-color: lightgray;
    border-radius: 3px;
    &_shadow {
        box-shadow: 0.4em 0.4em 5px rgba(204, 198, 198, 0.5);
    }
    &__inner {
        border-radius: 3px;
        height: 100%;
        width: 50%;
        background-color: $main-color;
    }
}