.dark-theme {
        background-color: black;
        color: wheat;
        text-shadow: 2px 2px 10px wheat,-2px -2px 10px wheat ;
        .menu__block {
          background-color: black;
          color: wheat;
        }
        svg:not(svg.skill__img) path {
            fill: wheat;
        }
        & svg:hover:not(svg.skill__img) path {
            fill: white;
        }
        .sidepanel__divider {
            background-color: wheat;
        }
        .menu__overlay {
           background-color:#2c2727; 
        }
        // #promo {
        //     background-image: url(../../img/tree.jpeg);
        // }
        .hamburger__line {
            background-color: #fff;
            box-shadow: 2px 2px 10px wheat;
        }
        #scroll_value {
            background-color: #333;
            border: 3px solid wheat;
            color:wheat;
        }
        #scroll_line {
            background-color: #333;
            border-bottom-color: wheat;
            box-shadow: -2px -2px 10px wheat;
        }
  
        img {
            box-shadow: 2px 2px 20px wheat;
            border-radius:  10px;
        }
        :after,:before {
            background-color: wheat;
        }
        .tooltip:after {
          background: #333;
          color: #fff;
        }
        .tooltip:before {
            background-color: initial;
        }
        .divider {
            background-color: wheat;
            box-shadow: 2px 2px 10px wheat;
        }
        .circle_bg_or {
            background-color: #e3970c;
        }
        .link_bg_or {
            background-color: #e3970c;
        }
        .skill {
            box-shadow: 2px 2px 20px wheat;
        }
        .arrow-up {
            border-color: wheat;
            box-shadow: 2px 2px 15px wheat;
            &:hover {
                box-shadow:  2px 2px 20px wheat;
            }
        }
        input,textarea {
            background-color: #e3970c;
            border-radius: 5px;
            font-weight: bold;
        }
        label {
            background-color: black;
            border-radius: 5px;
            color: wheat;
        }

       
  
}