.promo {
    min-height: 100%;
    background: url("../../img/lodka.jpg?as=jpeg") center center/cover no-repeat;
    &__wrapper {
        margin-top: 25px;
        width: 250px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__inner {
        position: relative;
        padding-top: 200px;
        min-height: 100vh;
    }
}
.display_overlay {
    position: fixed;
    min-height: 100%;
    min-width: 100%;
    background: url("../../img/tree.jpeg?as=jpeg") center center/cover no-repeat;
    right: -100%;
    top:0;
    transition: right 0.4s;
    &_show {
        right: 0;
    }
}  
.display_overlay_light {
    position: fixed;
    min-height: 100%;
    min-width: 100%;
    background: url("../../img/lodka.jpg?as=jpeg") center center/cover no-repeat;
    right: -100%;
    top:0;
    transition: right 0.4s;
    &_show {
        right: 0;
    }
} 
.promo__dark{
    background-image: url("../../img/tree.jpeg?as=jpeg");
}

