.tooltip {
    
    &:before, &:after {
        user-select: none;
        pointer-events: none;
        position: absolute;
        display: none;
        opacity: 0;
        /* opinions */
        text-transform: none; 
        //font-size: .9em;
    }
    &:before { //треугольник
        content: '';
        z-index: 1001;
        border: 5px solid transparent;
    }
    &:after {
        content: attr(data-tooltip);
        z-index: 1000;
        // пусть  сами определяют размеры по содержимому подсказки
        min-width: 3em;
        max-width: 21em;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        padding: 1ch 1.5ch;
        border-radius: .3ch;
        box-shadow: 0 1em 2em -.5em rgba(0, 0, 0, 0.35);
        background: #333;
        color: #fff;
        font-weight: bold;
    }
    &.flowUp:hover, &.flowDown:hover {
        &:after, &:before {
            display: block;
            animation: 
            tooltips-vert 
            300ms 
            ease-out
            forwards;
        } 
    }
    &.flowLeft:hover, &.flowRight:hover {
        &:after, &:before {
            display: block;
            animation: 
            tooltips-horz 
            300ms 
            ease-out 
            forwards;
        } 
    }
    //располагаем наверху от элемента.
    //--------------------------------------------------
    &.flowUp:before {
        bottom: 100%;
        border-bottom-width: 0;
        border-top-color: #333;
    }
    &.flowUp:after {
        bottom: calc(100% + 5px);
    }
     &.flowUp:after, &.flowUp:before {
        left: 50%;
        transform: translate(-50%, -.5em);
    }
    //---------------------------------------------- 
    //располагаем внизу от элемента.
    &.flowDown:before {
        top: 100%;
        border-top-width: 0;
        border-bottom-color: #333;
    }
    &.flowDown:after {
        top: calc(100% + 5px);
    }
     &.flowDown:after, &.flowDown:before {
        left: 50%;
        transform: translate(-50%, -.5em);
    }
    //-----------------------------------------------------
    //располагаем слева от элемента.
    &.flowLeft:before {
        top: 50%;
        border-right-width: 0;
        border-left-color: #333;
        left: calc(0em - 5px);
        transform: translate(-.5em, -50%);
    }
    &.flowLeft:after {
        top: 50%;
        right: calc(100% + 5px);
        transform: translate(-.5em, -50%);
    }
    //------------------------------------------------------
    //располагаем справа от элемента.
    &.flowRight:before {
        top: 50%;
        border-left-width: 0;
        border-right-color: #333;
        right: calc(0em - 5px);
        transform: translate(.5em, -50%);
    }
    &.flowRight:after {
        top: 50%;
        left: calc(100% + 5px);
        transform: translate(.5em, -50%);
    }
}
    //---------------------------------------------------------
@keyframes tooltips-vert {
    to {
      opacity: .9;
      transform: translate(-50%, 0);
    }
  }
  @keyframes tooltips-horz {
    to {
      opacity: .9;
      transform: translate(0, -50%);
    }
  }