.sidepanel {
   
    height: 340px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    z-index: 1000;
    &__body {
        position: fixed;
        top:100px;
        left:50px;
    }
    &__link {
        
    }
    &__divider {
        height: 70px;
        width: 2px;
        background: #000;
    }
    &__text {
        width: 25px;
        height: 150px;
    }    
    &__rotate {
        display: block;
        transform: rotate(-90deg) translate(-30%, -260%);
        width: 150px;
    }
}