.skills {
    padding-bottom: 120px;
    &__wrapper {
      margin-top: 70px;   
      display: grid;
      grid-template-columns: repeat(auto-fill,270px);
      gap:13px 13px;
      place-content: center center;   
    } 
    &__usage-skills {
        margin-top: 120px;
        display: grid;
        grid-template-columns: repeat(auto-fill,350px);
        gap: 30px 45px;
        place-content: center center;   
    }
}