.about {
    padding-bottom: 60px ;
    padding-top: 60px;
    // elements
    &__wrapper {
        display: grid;
        grid-template-columns: 400px 670px;
        grid-template-rows: minmax(245px, auto) minmax(255px, auto);
        grid-auto-rows: minmax(245px, auto);
        column-gap: 70px;
        row-gap: 40px;
    }
    &__photo {
        position: relative;
        grid-row: 1/3;
    }
    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &__desc {

    }
    &__text {
        margin-top: 20px;
        font-size: 14px;
        line-height: 21px;
    }
    &__skills {
        display: grid;
        grid-template: repeat(2,minmax(115px, auto))/ repeat(2,260px);
        grid-auto-rows: minmax(115px, auto);
        column-gap: 50px;
        row-gap: 20px;
    }
    &__skill {
        display: grid;
        grid-template-columns: 40px 200px;
        column-gap: 20px;
    }
    &__skill-text {
        font-size: 12px;
        line-height: 18px;
        margin-top: 15px;
    }
}