.link {
   
    &_bg_or {
        background-color: $main-color;
    }
    &_style {
        color: #000;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
    }   
    &_fz_14 {
        font-size: 14px;
    }
    &_type_btn {
        border-radius: 4px;
        padding: 12px 26px;
        height: 45px;
        outline: none;
        border: none;
    }
    &:hover {
        color:#000;
        text-decoration: none;
    }
}
