.hamburger {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;
        cursor: pointer;
        // modifires
        &_size {
            width: 30px;
            height: 21px;
        }
        &_hidden {
            display: none;
        }
        //position
        &__promo-inner {
            position: absolute;
            top:20px;
            right: 30px;
        }
        //elements
        &__line {
            display: block;
            width: 30px;
            height: 3px;
            background-color: black;
            &_long {
            width: 36px;     
            }
        }
}