.works {
   padding-bottom: 74px; 
   &__wrapper {
       margin-top: 60px;
       display: grid;
       grid-template: repeat(2,280px)/repeat(4,280px); 
       grid-auto-rows: 280px;
       gap: 5px 5px;
   }
   &__item {
        transition: all 0.3s;
        &_vertical {
            grid-row: 1/3;
            grid-column: 4/5;
        }
        &_horizontal {
            grid-column: 2/4;
            grid-row: 2/3;
        }
        &:hover {
            //transform: scale(1.01);
            filter:  grayscale(100%);
        }
    }
    &__img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
}