.price {
    padding-bottom: 60px;
    &__divider {
        margin-top: 30px;
    }
    &__wrapper {
        margin-top: 70px;
        display: grid;
        padding: 0 50px 0 50px;
        grid-template-columns: repeat(2,370px);
        grid-template-rows: repeat(3,minmax(45px,auto));
        grid-auto-rows: minmax(45px, auto);
        gap: 25px;
        justify-content: space-between;

    }
    &__item {
        display: grid;
        grid-template-columns: repeat(2, auto);
        grid-template-rows: repeat(2,minmax(21px,auto));
        justify-content: space-between;
    }
    &__price {
        font-size: 14px;
        font-weight: 700;
        justify-self: end;
    }
    &__desc {
        font-size: 12px;
        font-weight: 500;
        grid-column: 1/3;
        padding-left: 30px;
    }
   
}