.skill {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 30px;
    transition: all 0.1s;
    &_size {
        width: 270px;
        height: 270px;
    }
    &_border {
        border:  1px solid #000;
        border-radius: 6px;
    }
    &_shadow {
        box-shadow: 0.4em 0.4em 5px rgba(122,122,122,0.5);
    }
    &:hover {
        box-shadow: none;
        transform: translate(1px ,1px);
    }
    &__img {
        width: 100%;
        height: 100%;
    }
    &__title {

    }
    &__desc {
        font-size: 12px;
    }
}