@media (min-width:1439px) {
    .container {
        max-width: 1200px;
    };
}
@media(max-width:1440px) {
    .container {
        max-width: 1200px;
        padding-left: 150px;
    };
    .about {
        &__wrapper {
            grid-template-columns: 400px 570px;
        }
    }
    .skills {
        &__usage-skills {
            margin-top: 60px;
            column-gap: 20px;
            place-content: center space-around;
        }
    }
    .works {
        &__wrapper {
            grid-template: repeat(2,280px)/repeat(4,240px); 
        }
        &__item {
            &_vertical {
                grid-row: 1/3;
                grid-column: 4/5;
            }
            &_horizontal {
                grid-column: 2/4;
                grid-row: 2/3;
            }
        }
    }
    .contact {
        &__wrapper {
            grid-template-columns: 350px 585px;
            justify-content: unset;
            column-gap: 50px;
        }
    }
 }

@media (max-width: 1200px) {
    .works {
        &__wrapper {
            grid-template: repeat(2,280px)/repeat(4,200px); 
        }
    }
    .container {
        max-width: 960px;
        padding-left: 75px;
    }
    .sidepanel {
        display: none;
    }
    .arrow-up {
        width: 50px;
        height: 50px;
        border: 2px solid #BFE2FF;
        top: calc(90% - 35px);
    }
    .about {
        &__text {
            font-size: 13px;
            line-height: 20px;
        }
        &__wrapper {
            grid-template-columns: 350px 500px;
            column-gap: 35px;
            row-gap: 15px;
        }
        &__skills {
            grid-template: repeat(2, minmax(115px, auto))/repeat(2, 240px);
            column-gap: 10px;
        }
        &__skill {
            grid-template-columns: 40px 195px;
            column-gap: 15px;
        }
      
        &__skill-text {
            font-size: 11px;
        }
    }
    .title {
        &_fz_14 {
            font-size: 13px;
        }
        &_fz_36 {
            font-size: 34px;
        }
    }
    .scroll-indicator {
        &__value {
            height: 70px;
            width: 70px;
            line-height: 70px;
            font-size: 20px;
            left: 9px;
        }
    }
    .resume {
        
        &__wrapper {
            padding: 30px 0 0 0;
            column-gap: 60px;
            grid-template-columns: repeat(2,minmax(400px,auto));
        }
    }
    .contact {
        &__wrapper {
            grid-template-columns: 350px 350px;
            column-gap: 30px;
        }
        &__input-wrapper {
           grid-column: 1/3;
        }
        &__form {
            display: grid;
            grid-template-columns: 240px 240px;
            row-gap: 15px;
          
            &__area-wrapper {
                grid-column: 1/3;
                justify-self: stretch;
                position: relative;
                z-index: 1000;
            }
        }
    }
}
@media (max-width:992px) {
    .container {
        max-width: 720px;
        padding-left: 0px;
    }
    .scroll-indicator {
        &__value {
           display: none;
        }
    }
    .about {
        &__text {
            font-size: 13px;
            line-height: 20px;
        }
        &__wrapper {
            grid-template-columns: 300px 380px;
            column-gap: 35px;
            row-gap: 15px;
        }
        &__skills {
            grid-template: repeat(3, minmax(115px, auto))/repeat(1, 380px);
            column-gap: 10px;
            row-gap: 0;
        }
        &__skill {
            grid-template-columns: 40px 320px;
            column-gap: 15px;
            row-gap: 0;
        }
      
        &__skill-text {
            font-size: 11px;
        }
    }
    .title {
       &_fz_36 {
            font-size: 30px;
        }
    }
    .arrow-up {
        &__active {
            right: 12px;
        }
    }
    .resume {
       
        &__wrapper {
            padding: 30px 0 0 0;
            column-gap: 60px;
            grid-template-columns: repeat(1,400px);
            place-content: center center;
        }
        &__column {
            padding-top: 30px;
        }
    }
    .skills {
        &__usage-skills {
            margin-top: 60px;
            grid-template-columns: repeat(auto-fill, 350px);
            column-gap: 20px;
        }
    }
    .works {
        &__wrapper {
            grid-template: repeat(2,280px)/repeat(auto-fill,320px);
            place-content: center center; 
            gap: 20px 20px;
        }
        &__item {
            box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, 
            rgba(0, 0, 0, 0.09) 0px 4px 2px, 
            rgba(0, 0, 0, 0.09) 0px 8px 4px, 
            rgba(0, 0, 0, 0.09) 0px 16px 8px,
            rgba(0, 0, 0, 0.09) 0px 32px 16px;
            &_vertical {
                grid-row: unset;
                grid-column: unset;
            }
            &_horizontal {
                grid-column: unset;
                grid-row: unset;
            }
        }
    }
    .price {
        &__wrapper {
            grid-template-columns: repeat(2,auto);
 
        }
    }
    .contact {
        &__wrapper {
            grid-template-columns: 340px 340px;
            column-gap: 30px;
        }
        &__input-wrapper {
           grid-column: 1/3;
        }
        &__form {
            display: grid;
            grid-template-columns: auto auto;
            row-gap: 10px;
          
            &__area-wrapper {
                grid-column: 1/3;
                justify-self: stretch;
                position: relative;
                z-index: 1000;
            }
        }
        &__trigger {
            grid-template-columns: auto;
            grid-template-rows:  repeat(2, auto);
            row-gap: 10px;
            column-gap: unset;
        }
        &__area {
            height: 100px;
        }
        .link {
            order:2;
            width:62%;
            padding-top:10px;
        }
        &__policy-container {
           justify-content: start;
           padding-left: 0;
           order:1
        }
    }
  }
@media (max-width: 768px) {
    .container {
        max-width: 540px;
        padding-left: 0px;
    }
    .title {
        line-height: 40px;
        &_fz_48 {
            font-size: 30px;
        }
        &_fz_36 {
            margin-top: 15px;
        }
