        &_size_l {
            width: 59px;
            height: 23px;  
         }
        &__slider {
            &_round_l {
                border-radius: 34px;
            }
            &_pos_l:before {
                left: 0;
                bottom: 0; 
            }
        } 
         &__input:checked + &__slider_size_l:before {
            -webkit-transform: translateX(35px);
            -ms-transform: translateX(35px);
            transform: translateX(35px);
        }
    }
    .dark-theme {
       text-shadow: 2px 2px 10px wheat,-2px -2px 20px wheat ;
    }
    .about {
        padding-top: 40px;
