        &__trigger {
            grid-template-columns: auto;
            grid-template-rows:  repeat(2, auto);
            row-gap: 10px;
            column-gap: unset;
        }
        &__area {
            height: 100px;
        }
        .link {
            order:2;
            width:62%;
            padding-top:10px;
        }
        &__policy-container {
            justify-content: start;
           padding-left: 0;
           order:1
        }
    }
    .contact {
        &__wrapper {
            grid-template-columns: 99% ;
            column-gap: unset;
            place-content: center center;
        }
        &__input-wrapper {
           grid-column: 100%;
        }
        &__wrapper-inner {
            margin-top: 20px;
        }
        &__form {
            display: grid;
            grid-template-columns: auto auto;
            row-gap: 10px;
          
