                grid-column: 1/3;
                justify-self: stretch;
                position: relative;
                z-index: 1000;
            }
        }
        &__trigger {
            grid-template-columns: auto;
            grid-template-rows:  repeat(2, auto);
            row-gap: 10px;
            column-gap: unset;
        }
        &__area {
            height: 100px;
        }
        .link {
            order:2;
            width:62%;
            padding-top:10px;
        }
        &__policy-container {
           justify-content: start;
           padding-left: 0;
           order:1
        }
    }
  
}
