.menu {
    position: fixed;
    left:-100%;
    top:0;
    width: 100%;
    height: 100vh;
    z-index: 1000;
    visibility: hidden;
    opacity: 0;
    transition: all .6s;
    // add class
    &.active {
        left: 0;
        visibility: visible;
        opacity: 1;
    }
    // elements
    &__close {
        position: absolute;
        top:25px;
        right: 25px;
        cursor: pointer;
    }
    &__overlay {
        position: absolute;
        top:0;
        left:0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        opacity: .75;
    }
    &__block {
        position: relative;
        padding-top: 140px;
        padding-left: 40px;
        z-index: 1000;
        &_left {
           //transition: left .6s;
        }
        &_size {
            width: 345px;
            height: 100%;
        }
        &_style {
            background-color:$main-color;
        }
    }
    &__list {
       list-style-type: none;
       padding-left: 35px;
    }
    &__item {
        position: relative;
        margin-bottom: 15px;
        &:before {
            content: "";
            position: absolute;
            left:-35px;
            top:10px;
            width: 15px;
            height: 15px;
            border: 1px solid black;
            border-radius: 100%;
            transition:   background-color .6s;
        }
        &:hover {
            &:before {
                background-color: #fff;
            }
        }
    }
    &__link {
        font-weight: bold;
        font-size: 24px;
        line-height: 36px;
        color:#fff;
        &:hover {
            color:#000;
        }
    }
    &__social {
        position: absolute;
        bottom: 50px;
        right: 50%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        left: 50%;
        transform: translateX(-50%);
        &_size {
            width: 90px;
        }
       
    }
}